<template>
    <div>
        <h1 class="display-2 mb-3 mt-5">Histórico de Iniciativas</h1>
        <div class="main-bg">
            <!-- Iterate over initiatives and create a card for each -->
            <div v-for="(initiative, index) in initiatives" :key="index" class="card-voluntario">
                <div class="icon-wrapper">
                    <i class="fa-solid fa-user"></i>
                    <p>{{ initiative.nomeResponsavel }}</p>
                </div>
                <div class="icon-wrapper">
                    <i class="fa-solid fa-file-lines"></i>
                    <p>{{ initiative.designacao }}</p>
                </div>
                <div class="icon-wrapper">
                    <i class="fa-solid fa-location-dot"></i>
                    <p>{{ initiative.local }}</p>
                </div>
                <div class="icon-wrapper">
                    <i class="fa-solid fa-calendar-days"></i>
                    <p>{{ initiative.data }}</p>
                </div>
                <!-- Add any other content you want for each card -->
            </div>
        </div>
        <!-- Popup de alerta -->
        <b-alert v-model="showAlert" :variant="alertVariant" dismissible fade>
            {{ alertMessage }}
        </b-alert>
    </div>
</template>

<script>
export default {
    data() {
        return {
            initiatives: [], 
            showAlert: false, 
            alertVariant: '', 
            alertMessage: '', 
            selectedInitiative: '',
            quantidade: '',
        };
    },
    mounted() {
        this.loadInitiative();
    },
    methods: {
        loadInitiative() {
            const storedInitiative = localStorage.getItem('currentInitiative');
            if (storedInitiative) {
                this.initiative = JSON.parse(storedInitiative);
                console.log('Current Initiative:', this.initiative);
                console.log(this.initiative.tipo);
                this.selectedInitiative = this.initiative.tipo;
                // Push the loaded initiative to the initiatives array
                this.initiatives.push(this.initiative);
            } else {
                // Handle the case when there's no stored initiative
                console.log('No stored initiative found.');
            }
        },
    }
};
</script>


<style scoped>
.card-voluntario {
    margin-left: 10rem;
    margin-bottom: 20rem;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 25rem;
}

.icon-wrapper {
    display: flex;
    align-items: center;
}
.icon-wrapper i {
    margin-right: 5px; /* Adjust margin as needed */
}

i {
    margin-right: 40px;
}
</style>